<script>
    import QRCode from "@puckwang/qr-code";

    export default {
        metaInfo() {
            return {
                title: this.$i18n.t('title.qrCode') + " | " + this.$i18n.t('summary.qrCode'),
                meta: []
            }
        },
        data() {
            return {
                qrStyle: {
                    backgroundColor: "#FFFFFF",
                    fill: "#000000",
                    size: 300,
                    shape: "rect"
                },
                qrConfig: {
                    ecc: 'M'
                },
                qrType: 'url',
                qrData: {
                    email: {
                        subject: "",
                        body: "",
                        address: ""
                    },
                    url: "",
                    phone: "",
                    wifi: {
                        type: 'WPA',
                        ssid: "",
                        password: ""
                    }
                }
            }
        },
        methods: {
            generate() {
                let options = {
                    styleOptions: this.qrStyle,
                    qrCodeOptions: {
                        errorCorrectionLevel: this.qrConfig.ecc
                    }
                }

                let text = this.parseText();

                this.qrCodeCanvas.create("qrcodeCanvas", text, options);
            },
            download() {
                this.qrCodeCanvas.download("qrcode.png");
            },
            parseText() {
                let qrData = this.qrData;
                switch (this.qrType) {
                    case 'url':
                        return qrData.url;
                    case 'text':
                        return qrData.text;
                    case 'phone':
                        return `TEL:${qrData.phone}`;
                    case 'email':
                        return `MATMSG:TO:${qrData.email.address};SUB:${qrData.email.subject};BODY:${qrData.email.body};`;
                    case 'wifi':
                        return `WIFI:T:${qrData.wifi.type};S:${qrData.wifi.ssid};P:${qrData.wifi.password};;`;
                }
            }
        },
        computed: {},
        watch: {},
        components: {},
        mounted() {
            this.qrCodeCanvas = new QRCode();
        }
    }
</script>

